import Doc from './Doc'
import Login from './Login'
import Home from './Home'
import Blog from './Blog'
import Comm from './Comm'
import Core from './Core'
import Tuto from './Tuto'
import Guide from './Guide'
import Api from './Api'
import Hooks from './Hooks'

const routes=[
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/doc',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core
            },
            {
                path:'/doc/guide',
                component:Guide
            },
            {
                path:'/doc/api',
                component:Api
            },
            {
                path:'/doc/hooks',
                component:Hooks
            }
        ]
    },
    {
        path:'/login',
        component:Login
    },
    {
        path:'/blog',
        component:Blog
    },
    {
        path:'/tuto',
        component:Tuto,
        pri:true
    },
    {
        path:'/comm',
        component:Comm
    }
];
export default routes;