import React from 'react';
import {BrowserRouter as Router,Redirect,Route, NavLink,Switch} from 'react-router-dom';
import './header.css';
import routes from'./router.config.js'

//私有路由的封装
function PrivateRoute({component:Com,...rest}){
    return (
        <Route {...rest} render={({location})=>
            localStorage.getItem('userinfo')?<Com {...rest}/>:(
                <Redirect to={{
                    pathname:'/login',
                    state:location
                }}
                />
            )
        }
        />
    )
}
//带子路由的封装
function RouteWithSubRoutes(route){
    if(route.pri){
        return <PrivateRoute {...route}/>
    }
    return (
        <Route
          path={route.path}
          render={props => (
            <route.component {...props} routes={route.routes} />
          )}
        />
      );
}
const App=()=> {
    return <Router>
        <header>
            <nav>
            <NavLink activeClassName='active' exact to='/'>React</NavLink>
            <NavLink activeClassName='active' to='/doc'>文档</NavLink>
            <NavLink activeClassName='active' to='/tuto'>教程</NavLink>
            <NavLink activeClassName='active' to='/blog'>博客</NavLink>
            <NavLink activeClassName='active' to='/comm'>社区</NavLink>
            </nav>
        </header>
        <Switch>
            {
                routes.map((route, i) =>
                    <RouteWithSubRoutes key={i} {...route}/>
                )                  
            }
        </Switch>
    </Router>
}
export default App;
export {RouteWithSubRoutes};

